.collection__item {
  width: 272px;
  height: 429px;
  margin: 0 0 37px 23.5px;
}
.single-container__item {
  min-width: 274px;
  margin-right: 29px;
}

.collection__item-photo-container {
  width: 100%;
  height: 352px;
  background-size: 100% auto;
  margin-bottom: 10px;
}

.collection__item-title {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 14px;
  color: #bdbdbd;
  margin: 0;
}

.collection__item-name {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 21px;
  margin: 0 0 7px 0;
}

.collection__item-price {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-size: 17.5px;
  line-height: 26px;
  display: flex;
  margin: 0;
}
