.signup__container{
  display: flex;
  align-items: center;
  height: 80vh;

}
.signup__formContainer {
  background-color: rgba(255, 255, 255, 0.35);
  width: 30vw;
  height: 70vh;
  margin: 0 auto;
  padding: 2rem;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-family: "FiraGO", sans-serif;
  box-shadow: 1px #555efb;
  position: relative;
}

.signup__formContainer h3 {
  font-size: 3.5rem;
  margin: 0.3em 0 0.6em 0;
  color: white;
  font-family: "Fraunces", serif;
}

.signup__formContainer-input {
  width: 85%;
  height: 3em;
  border-radius: 5px;
  border: none;
  margin-bottom: 1em;
  font-size: 1em;
  padding-left: 10px;
}

.signup__formContainer-input:last-of-type {
  margin-bottom: 2.3em;
}
.signup__formContainer-input:focus {
  outline: solid #555efb;
}

#signupForm {
  text-align: center;
}

.signup__form-btn {
  border: none;
  border-radius: 5px;
  background-color: #555efb;
  padding: 0.7em 6em;
  color: white;
  font-family: "FiraGO", sans-serif;
  font-size: 1.5em;
  cursor: pointer;
}

.signup__form-btn:hover {
  background-color: #3842ff;
}
.signup__form-btn:active {
  transform: scale(0.98);
  transition-duration: 0.5s;
  background-color: #2a33d6;
}
.signup__formContainer-fa-eye{
  position: absolute;
  left: 433px;
  top: 363px;
  cursor: pointer;
}

.fa-eye-confirm{
  top: 428px;
}
