  margin: 0 0 23.5px 0;
}

.blog__article-item {
  width: 350px;
  height: 329px;
  background-color: white;
  padding: 19px 19px 0 19px;
  border: 1px solid #f8f9fb;
}

.blog__article-item:hover {
  filter: brightness(95%);
  transition: all 0.5s ease;
  border: #c4c4c4 1px solid;
  cursor: pointer;
  scale: 1.02;
}

.blog__article-type {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 600;
