}

.article__title {
  font-family: "Oswald";
  font-weight: 400;
  font-size: 20.3px;
  line-height: 30.5px;
  margin: 0 0 25px 0;
}

.article__text {
  font-family: "Roboto";
  font-weight: 300;
  font-size: 12.5px;
  line-height: 150%;
  letter-spacing: 0.01em;
  margin: 0 0 40px 0;
}

.article__details {
  font-family: "Oswald";
  font-weight: 300;
  font-size: 10px;
  line-height: 265%;
  color: #4f4f4f;
  border-top: 3px solid black;
  display: block;
}

.advantages__list {
  height: 80px;
